.login{
    background-color: grey;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
}

.login_container{
    padding:100px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 2px 2px 2px 2px rgba(0.12,0.12,0.12, 0.12)
}

.login_container > button{
    margin-top: 20px;
    text-transform: inherit !important;
    background-color: lightcoral;
}